.navbar {
  z-index: 9999;
}

.bg-hover:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.navbar-toggler:focus {
  text-decoration: none;
  outline: 0;
  -webkit-box-shadow: 0 0 0 0rem;
  box-shadow: 0 0 0 0rem;
}

.nav-link:hover {
  text-decoration: underline;
}

.logo:hover {
  font-weight: bold;
}
